import uniPopup from '@/components/uni-popup/uni-popup.vue'
import uniPopupMessage from '@/components/uni-popup/uni-popup-message.vue'
import uniPopupDialog from '@/components/uni-popup/uni-popup-dialog.vue'
import {
	mapActions,
	mapState,
	mapGetters
} from 'vuex';
export default {
	components: {
		uniPopup,
		uniPopupMessage,
		uniPopupDialog,
	},
	data() {
		return {
			userName: '',
			foodList: [],
			current: 0,
			food_id: '1',
			text: ''
		}
	},
	computed: {
		...mapGetters(['openid', 'hasUser'])
	},
	onLoad() {
		uni.getUserInfo({
			provider: 'weixin',
			success: function(res) {
				console.log(res.userInfo.avatarUrl);
			}
		});
		uni.getSetting({
		   success(res) {
		      console.log(res.authSetting)
		   }
		})
		this.getFoodlist()
		this.get_openid().then(() => {
			//判断用户是否已选菜
			if (this.hasUser.is_select == 1) {
				uni.reLaunch({
					url: '/pages/selectList/index'
				});
			}
		})
	},
	methods: {
		...mapActions(['set_user', 'get_openid', 'get_foodlist', 'select_food']),
		getFoodlist() {
			uni.showLoading({
				title: ''
			})
			let data = {
				busId: '1'
			}
			this.get_foodlist(data).then(res => {
				this.foodList = res
				uni.hideLoading()
			})
		},
		radioChange(evt) {
			this.food_id = evt.target.value
			for (let i in this.foodList) {
				if (this.foodList[i].value === evt.target.value) {
					this.current = i;
					break;
				}
			}
		},
		setUser(done, val) {
			uni.showLoading({
				title: '设置中'
			})
			this.userName = val
			if (!val) {
				uni.showToast({
					title: '姓名不能为空',
					icon: 'none',
					duration: 1000
				});
				return
			}
			let data = {
				userName: val,
				openid: this.openid
			}
			this.set_user(data).then(res => {
				uni.hideLoading()
				uni.showToast({
					title: '设置成功',
					duration: 1000
				});
				done()
			})
		},
		editor_input(e) {
			this.text = e.target.text
		},
		submit() {
			if (!this.hasUser) {
				this.$refs.dialogInput.open()
				return
			}
			uni.showLoading({
				title: ''
			})
			let data = {
				text: this.text,
				food_id: this.food_id,
				openid: this.openid
			}
			this.select_food(data).then(res => {
				uni.hideLoading()
				uni.showToast({
					title: '选择成功',
					duration: 1000
				});
				uni.navigateTo({
					url: '/pages/selectList/index'
				});
			})
		},
	}
}
